import React, { useEffect, useState } from 'react'
import Table from './Table'
//import Form from './Form'

const DOMAIN = "memohat.xyz"

function App() {
    const [status, setStatus] = useState(null)
    const [isLoading, setLoading] = useState(true)

    async function getStatus() {
        const res = await fetch("https://mcapi.us/server/query?ip=" + DOMAIN, {mode: "cors"})
        console.log(res)
        let res_json = await res.json()
        if (!res_json.online) {
            try {
                const res = await fetch("https://memohat.xyz")
                res_json['idle'] = res.ok
            }
            catch (error) {
                console.log(error)

            }
        }
        setStatus(res_json)
        setLoading(false)
    }

    const statusMsg = isLoading ? "Loading..." : !status ? "Unable to reach API Server" : status.online ? "Running" : status.idle ? "Idle" : "Down"

    if (!status || !status.online) {
        return <div className="container">
            <h1>Status: {statusMsg}</h1>
            <button onClick={getStatus}>
                refresh
        </button>
        </div>
    }
    return <div className="container">
        <h1>Status: {statusMsg}</h1>
        <h1>Users: {status.players.now}/{status.players.max}</h1>
        <Table users={status.players.list} />
        <button onClick={() => setLoading(true)}>
            refresh
        </button>
    </div>
}

export default App
